import React, { Component } from "react";
import Link from 'gatsby-link';
import styles from './index.module.scss';

class IndexPage extends Component {

  render(){ 
    
    return <div className={"container"}>
    	
            	<div className={"list"}>
            	<Link className={"item"} to="/prvi/">1. Razred </Link>
            <Link className={"item"} to="/drugi/">2. Razred  </Link>
            <Link className={"item"} to="/treci/">3. Razred </Link>
            <Link className={"item"} to="/cetvrti/">4. Razred </Link>
            	</div>	
            
           </div>
  }   
}

export default IndexPage


